<i18n>
{
  "en": {
    "Withdraw": "Withdraw",
    "unit": "￥",
    "Balance": "Balance",
    "returnAccount": "Please choose to return your account.",
    "fristDescribe": "Can apply to refund the balance at any time, the estimated time to account 0-3 working days.",
    "alertDescribe": "Accounts with a zero balance need to be recharged. Are you sure you want to continue?",
    "item": "Cancel | Confirm",
    "refundApplication": "Successful refund application!",
    "secondDescribe": "You have successfully initiated a balance refund, pending confirmation of the operator will manually refund to you.",
    "CancellationWithdrawal": "Cancellation of withdrawal",
    "WeChat": "WeChat",
    "Alipay": "Alipay"
  },
  "zh": {
    "Withdraw": "提现",
    "unit": "元",
    "Balance": "余额",
    "returnAccount": "请选择退回账号",
    "fristDescribe": "可随时申请退还余额，预计到账时间0-3个工作日。",
    "alertDescribe": "余额为0的账户需要再次充值，确定继续?",
    "item": "取消 | 确定",
    "refundApplication": "申请退款成功",
    "secondDescribe": "您已成功发起一条余额退款，待运营人员确认后将手动退款给您。",
    "CancellationWithdrawal": "取消提现",
    "WeChat": "微信",
    "Alipay": "支付宝"
  }
}
</i18n>

<template>
  <div class="withdraw">
    <head-top :title='$t("Withdraw")' :go-back="true"></head-top>
    <div class="container">
      <div class="bg">
          <div class="count">
            <span class="num">{{count}}</span>
            <strong>{{ $t("unit") }}</strong>
          </div>
          <div class="ispay">
            {{ $t("Balance") }}
          </div>
      </div>
      <div class="pay">
        <div class="tips">{{ $t("returnAccount") }}</div>
        <ul>
          <li v-for="(item, index) in way" :key="item.type" @click="selectWay(item, index)" class="bomPixel">
            <span class="icon"><img :src="item.icon" alt=""></span>
            <span class="text">{{item.text}}</span>
            <span class="circle">
                <img v-show="index === activePayIndex" src="../../assets/wallet_images/click.png" alt="">
                <img v-show="index !== activePayIndex" src="../../assets/wallet_images/quan-default.png" alt="">
            </span>
          </li>
        </ul>
        <p class="ml">
          <img src="../../assets/wallet_images/caution.png" alt="">
          <span>{{ $t("fristDescribe") }}</span>
        </p>
      </div>
      <div class="btn" @click="doWithdraw">
        {{ $t("Withdraw") }}
      </div>
    </div>
    <!-- 提现弹窗 -->
    <message-box :messageBox="messageBox"
                  icon="withdraw"
                  :title="$t('Withdraw')"
                  :describe="$t('alertDescribe')"
                  :confirm="$tc('item',2)"
                  :cancel="$tc('item',1)"
                  @boxHandle="boxHandle"></message-box>
    <!-- 提现成功 -->
    <transition name="fade">
      <div v-show="showSuccess" class="success">
        <div class="column">
          <img src="../../assets/scanning_images/succeed.png" alt="">
          <span>{{ $t("refundApplication") }}</span>
          <p>￥ <strong>{{count}}</strong></p>
          <span class="des">{{ $t("secondDescribe") }}</span>
        </div>
        <div class="btn" @click="isBack">
          {{ $t("CancellationWithdrawal") }}
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
// import { mapGetters, mapActions } from 'vuex'
import headTop from '../../components/Header/index'
import messageBox from '../../components/MessageBox/index'
export default {
  name: 'withdraw',
  data () {
    return {
      count: 99,
      activePayIndex: 0,
      way: [
        {
          icon: require('../../assets/wallet_images/alipay.png'),
          text: this.$t('Alipay'),
          type: 'alipay'
        },
        {
          icon: require('../../assets/wallet_images/wechat.png'),
          text: this.$t('WeChat'),
          type: 'wepay'
        }
      ],
      messageBox: false, // 弹框显示隐藏
      showSuccess: false
    }
  },

  components: {
    headTop,
    messageBox
  },
  methods: {
    selectWay (item, index) {
      this.activePayIndex = index
    },
    doWithdraw () {
      this.messageBox = true
    },
    boxHandle (confirm) {
      if (confirm) {
        this.showSuccess = true
      }
      this.messageBox = false
    },
    isBack () {
      this.showSuccess = false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.withdraw{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    .bg{
      .wh(691px,284px);
      margin: 20px auto 40px auto;
      background: url('../../assets/wallet_images/txbg.png');
      background-size: cover;
      padding-top: 66px;
      font-size: 32px;
      .num{
        font-size: 82px;
        position: relative;
        strong{
          font-size: 30px;
          position: absolute;
          bottom: 10px;
          right: -50px;
        }
      }
      .ispay{
        margin-top: 20px;
        font-size: 30px;
        color: #AAAAAA;
      }
    }
    .pay{
      .tips{
        .wh-l(100%, 80px);
        text-align: left;
        padding-left: 30px;
        font-size: 30px;
        color: #aaaaaa;
      }
      ul{
        margin-bottom: 10px;
        li{
          .wh-l(100%, 90px);
          position: relative;
          span{
            display: block;
            img{
              vertical-align: middle;
            }
            &.icon{
              .wh-f(15%,100%);
              img{
                .wh(44px,44px);
              }
            }
            &.text{
              .wh-f(70%,100%);
              text-align: left;
              font-size: 32px;
              strong{
                font-size: 18px;
                color: red;
              }
            }
            &.circle{
              .wh-f(15%,100%);
              img{
                .wh(30px,30px);
              }
            }
          }
        }
      }
      .ml{
        height: 50px;
        line-height: 50px;
        font-size: 18px;
        text-align: left;
        position: relative;
        color: #AAAAAA;
        padding-left: 72px;
        img{
          height: 22px;
          position: absolute;
          left: 25px;
          top: 15px;
        }
        span{
          // max-width: 80%;
          .zoom-font(0.9);
          display: inline-block;
        }
      }
    }
  }
   .btn{
    border-radius: 10px;
    font-size: 30px;
    .wh-l(690px,100px);
    background: #f4da21;
    color: #585858;
    position: absolute;
    left: 50%;
    margin-left: -345px;
    bottom: 50px;
    box-shadow: 0px 4px 25px rgba(255, 219, 0, 0.6);
  }
  .success{
    .fs-noTop;
    .wh(100%, calc(~"100% - 90px"));
    z-index: 10;
    background: #FFF;
    .column{
      .wh(700px, 660px);
      margin: 20px auto 0 auto;
      position: relative;
      border-radius: 20px;
      box-shadow: 0px 4px 25px rgba(130,130,130,0.2);
      img{
        height: 180px;
        margin: 80px auto 62px auto;
      }
      span{
        display: block;
        font-size: 30px;
        &:last-child{
          font-size: 26px;
          color: #bdbdbd;
        }
      }
      p{
        margin: 20px 0 14px 0;
        font-size: 30px;
        strong{
          font-size: 60px;
          color: #f7223b;
        }
      }
      .des{
        font-size: 26px;
        .zoom-font(0.8);
        margin-left: 20%;
      }
    }
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
}
</style>



